export function genererMentionInfo(formData) {
  return `
MENTION D'INFORMATION RELATIVE  LA PROTECTION DES DONNES PERSONNELLES

Responsable du traitement : ${formData.responsable || '[ complter]'}
Contact DPO / responsable de la protection des donnes : ${formData.contact || '[ complter]'}
Finalit du traitement : ${formData.finalite || '[ complter]'}
Base lgale du traitement : ${formData.baseLegale || '[Ex : excution dun contrat, consentement, obligation lgale]'}

Catgories de donnes collectes :
${formData.categoriesDonnees.length > 0
    ? formData.categoriesDonnees.map(cat => `- ${cat}`).join('\n')
    : '[ complter]'}

Destinataires ou catgories de destinataires des donnes :
${formData.destinataires || '[ complter]'}

Dure de conservation des donnes : ${formData.dureeConservation || '[ complter]'}

Vos droits :
- Droit d'accs, rectification et effacement de vos donnes
- Droit  la limitation du traitement
- Droit  la portabilit des donnes
- Droit d'opposition
- Droit de retirer votre consentement  tout moment si la base lgale du traitement est le consentement
- Droit d'introduire une rclamation auprs de la CNIL ou de lautorit de contrle comptente

Modalits d'exercice de vos droits :
Vous pouvez contacter le DPO ou le responsable du traitement  l'adresse suivante : ${formData.contact || '[ complter]'}.

Transfert de donnes vers des pays tiers :
${formData.transfertHorsUE === 'oui'
    ? `Les donnes peuvent tre transfres vers : ${formData.paysTransfert || '[ complter]'}
Garanties mises en place : ${formData.garantiesTransfert || '[ complter]'}`
    : 'Aucun transfert hors de lUnion Europenne nest prvu.'}

---
Document gnr le ${new Date().toLocaleDateString('fr-FR')} avec le Gnrateur de Mentions d'Information
`.trim();
}
